<template>
<div class="centered-card">
                    <el-card class="box-card">
                        <div class="text item">
                            <el-form label-position="top" :model="apiForm" :size="formSize"
                                label-width="auto" status-icon>
                                <el-form-item label="API接口地址">
                                    <el-input v-model="apiForm.apiUrl" />
                                </el-form-item>
                                <el-form-item label="apiKey">
                                    <el-input v-model="apiForm.apiKey" />
                                </el-form-item>
                                <el-form-item label="app_code(为一些中转平台设置)">
                                    <el-input v-model="apiForm.app_code" />
                                </el-form-item>
                                <el-form-item label="模型名称">
                                    <el-input v-model="apiForm.model" />
                                </el-form-item>
                                <el-form-item>
                                    <el-button type="primary" @click="saveapi">
                                        保存
                                    </el-button>
                                </el-form-item>
                            </el-form>
                        </div>
                    </el-card>
                </div>
</template>
<script>
import { onMounted, ref } from 'vue'
import { ElMessage } from 'element-plus'
import axios from 'axios'

export default {
    setup() {
        const formSize = ref('default')
        const activeName = ref('first')

        const apiForm = ref({
            apiUrl: '',
            apiKey: '',
            app_code: '',
            model: '',
        });

        const saveapi = async () => {
            try {
                const response = await axios.post('/apiconfig', apiForm.value)
                const status = response.data.status
                const msg = response.data.msg
                if (status === 200) {
                    ElMessage.success(msg)
                } else {
                    ElMessage.error('修改失败！' + msg)
                }
            } catch (error) {
                ElMessage.error('修改失败！', error.message)
            }
        }

        onMounted(async () => {
            try {
                const apiresponse = await axios.post('/getapiconfig')
                const apiconfigData = apiresponse.data.msg

                // 更新 Form 的值
                apiconfigData.forEach(item => {
                    apiForm.value[item.config] = item.value
                })

            } catch (error) {
                ElMessage.error('查询失败！', error.message)
            }
        })

        return {
            formSize,
            apiForm,
            activeName,
            saveapi,
        }
    },
}

</script>
<style scoped>
.centered-card {
    flex: 1; 
    display: flex;
    justify-content: center;
    align-items: center;
}

.item {
    padding: 20px;
    text-align: center;
}

.box-card {
    width: 100%;
}

@media screen and (min-width: 768px) {
    .box-card {
        width: 30%;
    }
}
</style>